import React from 'react';

import HostPlan from '@/assets/imgs/imgs/hosting_plan_background.png';
import './index.scss';

interface ContentWrapProps {
  paddingTop?: string;
  paddingRight?: string;
  paddingBottom?: string;
  paddingLeft?: string;
  imgBg?: boolean;
  blueBg?: boolean;
}

const ContentWrap: React.FC<ContentWrapProps> = ({
  paddingTop = '60px',
  paddingRight = 'calc((100% - 1140px) / 2)',
  paddingBottom = '110px',
  paddingLeft = 'calc((100% - 1140px) / 2)',
  imgBg = false,
  blueBg = false,
  children
}) => {
  return (
    <div
      style={{
        paddingTop,
        paddingRight,
        paddingBottom,
        paddingLeft,
        color: imgBg || blueBg ? '#fff' : '#304659',
        background: imgBg || blueBg ? '#0033cc' : '#fff'
      }}
      className="content-wrap"
    >
      {imgBg && <img src={HostPlan} alt="hosting_plan_background" />}
      <div>{children}</div>
    </div>
  );
};

export default ContentWrap;
