import React, { useState, useEffect, useCallback } from 'react'

import './index.scss'

const SHOW_BACK_TOP_ICON = 50

const BackToTop: React.FC = () => {
  const [showBack, toggleShowBack] = useState(false)

  const backToTop = useCallback(() => {
    if (!showBack) {
      return false
    }

    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth'
    })
  }, [showBack])

  useEffect(() => {
    const scroll = (e) => {
      if (SHOW_BACK_TOP_ICON < window.scrollY && !showBack) {
        toggleShowBack(true)
      }

      if (SHOW_BACK_TOP_ICON > window.scrollY && showBack) {
        toggleShowBack(false)
      }
    }

    window.addEventListener('scroll', scroll)

    return () => window.removeEventListener('scroll', scroll)
  }, [showBack])

  return (
    <div className="back-to-top">
      <div
        onClick={backToTop}
        className={['icon', showBack ? 'show' : 'hide'].join(' ')}
      ></div>
    </div>
  )
}

export default React.memo(BackToTop)
